<template>
  <div class="page-wrap">
    <div class="headTitle">
      <i @click="toPrePage" class="cubeic-arrow"></i>
      <p>{{$t('hl_helper_edit1')}}</p>
    </div>
    <!-- 中间部分 -->
    <div class="center_contain">
      <div class="top">
        <!-- 选择性别 -->
        <div class="form-item">
          <p>{{$t('sex_title_1')}}</p>
          <!-- @blur="genderBlur" -->
          <div
            @click="showPicker('gender','userData')"
            :class="{blank:userTotalInfo.userData.gender!='Gender'}"
            class="GenderDiv"
            ref="Gender"
          >{{userTotalInfo.userData.gender}}</div>
        </div>
        <p>{{$t('account_info_lastname2')}}</p>
        <input
          type="text"
          v-model="userTotalInfo.userData.Surname"
          placeholder="Passport Surname (English)*"
        />
        <p>{{$t('account_info_firstname2')}}</p>
        <input
          type="text"
          v-model="userTotalInfo.userData.Givenname"
          placeholder="Passport Given Name (English)*"
        />
        <!-- 日期插件 -->
        <div class="form-item">
          <p>{{$t('Date_of_Birth')}}</p>
          <!-- @blur="birthDateBlur" -->
          <div
            ref="datebirth"
            :class="{blank:userTotalInfo.userData.datebirth!='Date of birth'}"
            class="GenderDiv"
            @click="showDatePicker('datebirth')"
          >{{userTotalInfo.userData.datebirth}}</div>
        </div>
        <!-- Position -->
        <div class="form-item">
          <p>{{$t('hl_helper_desc25')}}</p>
          <!-- @blur="genderBlur" -->
          <div
            @click="showPicker('Position','userData')"
            :class="{blank:userTotalInfo.userData.Position!='Position'}"
            class="GenderDiv"
            ref="Position"
          >{{this.userTotalInfo.userData.Position}}</div>
        </div>
        <!-- Nationality -->
        <div class="form-item">
          <p>{{$t('hl_helper_desc27')}}</p>
          <!-- @blur="genderBlur" -->
          <div
            @click="showPicker('Nationality','userData')"
            :class="{blank:userTotalInfo.userData.Nationality!='Nationality'}"
            class="GenderDiv"
            ref="Nationality"
          >{{this.userTotalInfo.userData.Nationality}}</div>
        </div>
        <div class="form-item">
          <p>{{$t('Effective_date')}}</p>
          <!-- @blur="birthDateBlur" -->
          <div
            ref="Effective"
            :class="{blank:userTotalInfo.userData.Effective!='Effective date'}"
            class="GenderDiv"
            @click="showDatePicker('Effective')"
          >{{userTotalInfo.userData.Effective}}</div>
        </div>
      </div>
      <div class="text">
        <p>
          {{$t('hl_helper_desc11')}}
          <span>{{$t('hl_helper_desc10')}}</span>
        </p>
      </div>
      <!-- 地址填写 -->
      <div class="table_address">
        <div class="content">
          <div
            class="terntory"
            :class="{blank:userTotalInfo.address.Territory!='Territory'}"
            @click="showPicker('Territory','address')"
          >{{userTotalInfo.address.Territory}}</div>
          <div
            class="distnct"
            :class="{blank:userTotalInfo.address.District!='District'}"
            @click="showPicker('District','address')"
          >{{userTotalInfo.address.District}}</div>
          <div class="block">
            <input type="text" v-model="userTotalInfo.address.block" placeholder="Block" />
            <input type="text" v-model="userTotalInfo.address.floor" placeholder="Floor" />
            <input type="text" v-model="userTotalInfo.address.room" placeholder="Room" />
          </div>
          <div class="building">
            <input
              type="text"
              v-model="userTotalInfo.address.building"
              placeholder="Building/Estate/Street No*"
            />
          </div>
        </div>
      </div>
      <!-- 选择HKID按钮和passport Number按钮 -->
      <div>
        <button class="HKID" @click="inputHkid" :class="[select==1?'blue':'']">
          {{$t('personal_hkid')}}
          <i :class="[select==1?'cubeic-ok':'']"></i>
        </button>
        <button class="passport" @click="inputPassport" :class="[select==2?'blue':'']">
          {{$t('info_passport')}}
          <i :class="[select==2?'cubeic-ok':'']"></i>
        </button>
      </div>
      <!-- 输入框 -->
      <div class="HK">
        <span class="hkNumber">{{select==1?`${$t('account_info_hkid')}:`:`${$t('info_passport')}:`}}</span>
        <div class="right" v-show="select == 1">
          <span>
            <!-- @blur="HkidBlur" -->
            <input
              v-model="userTotalInfo.form.HKID1"
              class="IDnumber"
              ref="HKID1"
              type="text"
              placeholder="A123456"
            />
          </span>
          <span v-show="select == 1">(</span>
          <span v-show="select == 1">
            <input
              ref="HKID2"
              v-model="userTotalInfo.form.HKID2"
              class="num"
              type="text"
              placeholder="7"
            />
          </span>
          <span v-show="select == 1">)</span>
        </div>
        <div class="right" v-show="select == 2">
          <span>
            <!--  @blur="passportBlur" -->
            <input
              ref="HKID_passport"
              v-model="userTotalInfo.form.HKID_passport"
              :class="[select==2?'width':'']"
              class="IDnumber"
              type="text"
              placeholder="A123456"
            />
          </span>
        </div>
      </div>
    </div>
    <!-- 底部按钮 -->
    <div class="button-bar">
      <button @click="confirm" class="proceed">Confirm</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      //userData
      userTotalInfo: {
        userData: {
          gender: "Gender",
          Surname: "",
          Givenname: "",
          datebirth: "Date of birth",
          Position: "Position",
          Nationality: "Nationality",
          Effective: "Effective date"
        },
        //address
        address: {
          Territory: "Territory",
          District: "District",
          block: "",
          floor: "",
          room: "",
          building: ""
        },
        form: {
          HKID_passport: "",
          HKID1: "",
          HKID2: ""
        }
      },

      //判断点击之后的按钮
      select: 1,
      //保存年龄
      ages: "",
      //保存日期选择的输入框
      data_type: "",
      //保存性别,position,nationality的输入框
      data_type_two: "",
      //保存选中的是address还是userData
      isuserData: ""
    };
  },
  created() {
    this.getProtectorInfo();
    console.log(this.userTotalInfo);
  },
  methods: {
    ...mapMutations(["setProtectorInfo", "setHelperInfo"]),
    //点击选择性别---------------------------------------------------/////
    showPicker(type, type2) {
      this.data_type_two = type;
      this.isuserData = type2;
      let column1 = [];
      if (type == "gender") {
        column1 = [
          { text: this.$t("Female"), value: "Male" },
          { text: this.$t("Male"), value: "Female" }
        ];
      } else if (type == "Position") {
        column1 = [
          { text: "Domestic Helper", value: "Domestic Helper" },
          { text: "Gardener", value: "Gardener" },
          { text: "Others", value: "Others" }
        ];
      } else if (type == "Nationality") {
        column1 = [
          { text: "Chinese", value: "Chinese" },
          { text: "Hong Kong", value: "Hong Kong" },
          { text: "Others", value: "Others" }
        ];
      } else if (type == "Territory") {
        column1 = [
          { text: "NEW TERRITORIES", value: "NEW TERRITORIES" },
          { text: "HONG KONG", value: "HONG KONG" },
          { text: "KOWLOON", value: "KOWLOON" }
        ];
      } else if (type == "District") {
        column1 = [
          { text: "North District", value: "North District" },
          { text: "Tai Po", value: "Tai Po" },
          { text: "Tsuen Wan", value: "Tsuen Wan" },
          { text: "Kai Hing", value: "Kai Hing" }
        ];
      }
      //改变插件中的默认title和data
      setTimeout(() => {
        this.picker.$updateProps({
          title: type,
          data: [column1]
        });
      }, 0);
      if (!this.picker) {
        this.picker = this.$createPicker({
          title: this.$t("Gender"),
          data: [column1],
          cancelTxt: this.$t("cancel"),
          confirmTxt: this.$t("confirm"),
          onSelect: this.selectGender,
          onCancel: this.cancelGender
        });
      }
      this.picker.show();
    },
    //选择性别点击确认事件
    selectGender(selectedVal, selectedIndex, selectedText) {
      this.userTotalInfo[this.isuserData][
        this.data_type_two
      ] = selectedText.join(" ");
    },
    //选择性别点击取消事件
    cancelGender() {},

    //点击日期插件---------------------------------------------------///////
    showDatePicker(type) {
      //改变插件中的默认title和data
      setTimeout(() => {
        this.datePicker.$updateProps({
          title: type
        });
      }, 0);
      //保存选择日期的是哪个输入框
      this.data_type = type;
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: this.$t("account_info_birthday"),
          min: new Date(1900, 1, 1),
          max: new Date(),
          value: new Date(1980, 5, 20),
          cancelTxt: this.$t("cancel"),
          confirmTxt: this.$t("confirm"),
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle
        });
      }
      this.datePicker.show();
    },
    //点击确认事件
    selectHandle(date, selectedVal, selectedText) {
      this.userTotalInfo.userData[this.data_type] = this.dateTozero(
        selectedVal
      ).join("-");
      //因为cube-ui再次选完日期不会修改日期的显示
      this.$refs[this.data_type].value = this.userTotalInfo.form.datebirth;
      console.log(this.userTotalInfo.userData);
      if (this.data_type == "datebirth") {
        this.getYears();
      }
    },
    //点击取消
    cancelHandle() {},
    //数字小于10时前面加个0
    dateTozero(arr) {
      return arr.map(item => (item < 10 ? "0" + item : "" + item));
    },

    //确认事件(判断信息)---------------------------------------------------//////
    async confirm() {
      console.log(this.userTotalInfo);
      //验证信息是否都填写完毕
      if (
        this.userTotalInfo.userData.datebirth == "Date of birth" ||
        this.userTotalInfo.userData.Givenname == "" ||
        this.userTotalInfo.userData.gender == "Gender" ||
        this.userTotalInfo.userData.Surname == "" ||
        this.userTotalInfo.userData.Position == "Position" ||
        this.userTotalInfo.userData.Nationality == "Nationality" ||
        this.userTotalInfo.userData.Effective == "Effective date" ||
        this.userTotalInfo.address.Territory == "Territory" ||
        this.userTotalInfo.address.District == "District" ||
        this.userTotalInfo.address.block == "" ||
        this.userTotalInfo.address.floor == "" ||
        this.userTotalInfo.address.room == "" ||
        this.userTotalInfo.address.building == ""
      ) {
        this.$createToast({
          txt: this.$t("notInformation"),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      if (
        this.userTotalInfo.form.HKID1 == "" &&
        this.userTotalInfo.form.HKID_passport == ""
      ) {
        this.$createToast({
          txt: this.$t("notInformation"),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      if (
        this.userTotalInfo.form.HKID1 != "" &&
        this.userTotalInfo.form.HKID2 == ""
      ) {
        this.$createToast({
          txt: this.$t("notInformation"),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      //判断是否大于18岁
      // if (this.ages < 18) {
      //   this.$createToast({
      //     txt: this.$t("policyholder_number"),
      //     time: 3000,
      //     type: "txt"
      //   }).show();
      //   return false;
      // }
      //调用验证方法,验证信息填写是否符合规定
      var isverify1 = await this.verifyUserFrom(this.userTotalInfo.form);
      if (isverify1 == false) {
        return false;
      }
      //如果验证都通过
      console.log(isverify1);
      if (isverify1 == true) {
        // this.form.gender = this.form.gender || "";
        // this.form.surname = this.form.surname || "";
        // this.form.givename = this.form.givename || "";
        // this.form.datebirth = this.form.datebirth || "";
        // this.form.HKID1 = this.form.HKID1 || "";
        // this.form.HKID2 = this.form.HKID2 || "";
        // this.form.HKID_passport = this.form.HKID_passport || "";
        // this.form.title = "Policyholder";
       // let newUserInfo = Object.assign({}, this.userTotalInfo);
        //把数据存到vuex中
        this.setHelperInfo(this.userTotalInfo);
        this.$router.push({ path: "/Question" });
      }
    },
    //如果vuex中有保单人信息,获取渲染到页面-----------------------------///
    getProtectorInfo() {
      console.log(this.helperInfo);
      //console.log(JSON.parse(JSON.stringify(this.helperInfo))['address']);
      if (this.helperInfo) {
        for (var key in this.helperInfo) {
          for (var key2 in this.helperInfo[key]) {
            this.userTotalInfo[key][key2] = this.helperInfo[key][key2];
          }
        }
        //判断再次进入信息编辑页的时候passport是否有值
        if (this.userTotalInfo.form.HKID_passport != "") {
          this.select = 2;
        }
      }
      this.getYears();
    },
    //封装判断用户输入的信息是否符合规定
    async verifyUserFrom(protectorInfo) {
      var isverify = false;
      //验证用户surname
      // var surnameinput = new RegExp("[a-zA-Z]");
      // if (!surnameinput.test(protectorInfo.surname)) {
      //   this.$createToast({
      //     txt: this.$t('LastNameIncorrect'),
      //     time: 3500,
      //     type: "txt"
      //   }).show();
      //   return false;
      // }
      //验证用户given name
      // var surnameinput1 = new RegExp("[a-zA-Z]");
      // if (!surnameinput1.test(protectorInfo.givename)) {
      //   this.$createToast({
      //     txt: this.$t('NameIncorrect'),
      //     time: 3500,
      //     type: "txt"
      //   }).show();
      //   return false;
      // }
      //验证passport
      // var passportInput = new RegExp("^[0-9]*$");
      // if (
      //   protectorInfo.HKID_passport.trim() != "" &&
      //   !passportInput.test(protectorInfo.HKID_passport)
      // ) {
      //   this.$createToast({
      //     txt: this.$t("passportErr"),
      //     time: 3500,
      //     type: "txt"
      //   }).show();
      //   return false;
      // }
      //验证HKID(调用接口)
      if (this.userTotalInfo.form.HKID1 != "") {
        let HKID =
          this.userTotalInfo.form.HKID1 + this.userTotalInfo.form.HKID2;
        //P1032651
        console.log(HKID);
        try {
          const res = await this.$http.post(
            "/wesupromo/web/validate/validateHKIDWithLng",
            {
              language: "tc",
              hkid: HKID
            }
          );
          console.log(res);
          console.log(res.status);
        } catch (err) {
          console.log(err);
          if (err.data.is_valid == false) {
            const toast = this.$createToast({
              time: 3500,
              type: "txt",
              txt: err.msg
            });
            toast.show();
            return false;
          }
        }
      }
      isverify = true;
      console.log(isverify);
      return isverify;
    },
    //点击返回按钮-----------------------------------------------------/////
    toPrePage() {
      this.$router.push("/EmployerDetail");
    },
    //处理身份证和护照按钮处理----------------------------------------/////
    inputHkid() {
      //如果有值就把select改为2
      if (this.$refs.HKID_passport.value.trim() != "") {
        this.$createToast({
          txt: this.$t("onlyOne"),
          time: 3000,
          type: "txt"
        }).show();
        this.select = 2;
      } else {
        this.select = 1;
      }
    },
    inputPassport() {
      if (
        this.$refs.HKID1.value.trim() != "" ||
        this.$refs.HKID2.value.trim() != ""
      ) {
        this.$createToast({
          txt: this.$t("onlyOne"),
          time: 3000,
          type: "txt"
        }).show();
        this.select = 1;
      } else {
        this.select = 2;
      }
    },
    // 获取当天的天数----------------------------------------------/////
    getToday(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "/" + m + "/" + d;
    },
    //计算年龄(是否大于十八岁)----------------------------------------/////
    getYears() {
      let todayDate = this.getToday(new Date());
      let years = Math.ceil(
        (new Date(`${todayDate} 00:00:00`).getTime() -
          new Date(
            `${this.userTotalInfo.userData.datebirth.replace(
              /-/g,
              "/"
            )} 00:00:00`
          ).getTime()) /
          (1000 * 60 * 60 * 24)
      );
      this.ages = Math.floor(years / 365);
      console.log(this.ages + "岁");
    }
  },
  computed: {
    ...mapState(["helperInfo", "product_id", "language", "plan"])
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
