  padding: 34px;
  background: rgba(250, 250, 250);
  padding-bottom: 260px;
  position: relative;
  padding-top: 120px;

  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: fixed;
    width: 100%;
    height: 200px;
    bottom: 0;
