    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250);
  }
  .proceed {
    width: 350px;
    height: 100px;
    line-height: 100px;
    margin-top: 50px;
    border: none;
    border-radius: 10px;
