    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .headTitle {
    position: fixed;
    height: 100px;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 99;
    background-color: rgba(250, 250, 250);
    border-bottom: 2px solid rgba(0, 0, 0, 0.25);
    .cubeic-arrow {
      color: rgb(0, 122, 255);
      display: inline-block;
      transform: rotateZ(180deg);
      font-size: 32px;
      font-weight: bold;
      position: absolute;
      left: 0px;
      line-height: 100px;
      width: 50px;
      text-align: center;
      height: 100%;
    }
    p {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
  }
  .center_contain {
    .top {
      p {
        font-size: 20px;
        margin-top: 30px;
        margin-bottom: 10px;
      }
      input {
        height: 60px;
        width: 660px;
        border-radius: 10px;
        border: 2px solid #ccc;
        padding-left: 10px;
        font-size: 20px;
        color: rgb(0, 0, 0);
        font-weight: bold;
      }
      input::-webkit-input-placeholder {
        color: #9b9b9b;
        letter-spacing: 2px;
        font-weight: 500;
      }
      .form-item {
        p {
          color: rgb(0, 0, 0);
        }
        margin-top: 20px;
      }
      .GenderDiv {
        height: 60px;
        width: 660px;
        border-radius: 10px;
        font-size: 20px;
        border: 2px solid rgba(155, 155, 155, 0.5);
        color: #9b9b9b;
        line-height: 60px;
        text-align: left;
        padding-left: 10px;
      }
      .blank {
        color: rgb(0, 0, 0);
        font-weight: bold;
      }
    }
  }
  .text {
    margin-top: 50px;
    p {
      font-size: 25px;
    }
    span {
      color: red;
    }
  }
  .table_address {
    width: 670px;
    margin-top: 20px;
    background-color: #fff;
    border: 2px solid rgba(23, 37, 54, 0.3);
    .content {
      margin-left: 20px;
      width: 649px;
      input {
        height: 80px;
        width: 100%;
        font-size: 20px;
        font-weight: bold;
      }
      input::-webkit-input-placeholder {
        color: #9b9b9b;
        letter-spacing: 2px;
        font-weight: 500;
      }
      .terntory {
        width: 100%;
        height: 81px;
        color: #9b9b9b;
        line-height: 81px;
        border-bottom: 1px solid rgba(23, 37, 54, 0.8);
        font-size: 20px;
      }
      .distnct {
        width: 100%;
        height: 81px;
        color: #9b9b9b;
        line-height: 81px;
        border-bottom: 1px solid rgba(23, 37, 54, 0.8);
        font-size: 20px;
      }
      .blank {
        color: rgb(0, 0, 0);
        font-weight: bold;
      }
      .block {
        width: 100%;
        display: flex;
        height: 61px;
        padding: 10px 0;
        border-bottom: 1px solid rgba(23, 37, 54, 0.8);
        input {
          flex: 1;
          height: 60px;
          border-right: 1px solid rgba(23, 37, 54, 0.8);
          &:last-child {
            border-right: 0;
            padding-left: 10px;
          }
          &:nth-child(2) {
            padding-left: 10px;
          }
        }
      }
      .building {
        width: 100%;
      }
    }
  }
  .HKID {
    width: 333px;
    height: 56px;
    color: white;
    font-size: 30px;
    outline: none;
    border: none;
    background: #9b9b9b;
    border-radius: 10px;
    margin-top: 40px;
  }
  .passport {
    width: 333px;
    height: 56px;
    padding: 0;
    text-align: center;
    color: white;
    font-size: 30px;
    background: #9b9b9b;
    outline: none;
    border: none;
    border-radius: 10px;
    margin-top: 40px;
  }
  .blue {
    background: @blue;
  }
  .HK {
    margin-top: 30px;
    font-size: 32px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    height: 70px;
    .hkNumber {
      display: inline-block;
      height: 70px;
      line-height: 70px;
    }
    .right {
      display: inline-block;
    }
    .hkNumber {
      color: rgb(0, 0, 0);
      // font-weight: bold;
      font-family: HelveticaNeue;
    }
    span {
      .IDnumber {
        width: 235px;
        height: 60px;
        border-radius: 13px;
        font-size: 30px;
        padding-right: 15px;
        border: 2px solid rgba(155, 155, 155, 0.5);
        margin-left: 20px;
        text-align: right;
      }
      .num {
        width: 75px;
        height: 60px;
        border-radius: 13px;
        font-size: 30px;
        border: 2px solid rgba(155, 155, 155, 0.5);
        text-align: center;
      }
      .width {
        width: 300px;
      }
    }
  }
}
</style>


